<template>
  <div class="article-wrap">
    <div class="article-size article-left-width">
      <span class="hot-article">文章列表</span>
      <ol>
        <li v-bind:class="{ articleList: articleLists, godColor: key==godColor }"
            ref="article"
            v-for="(item, key) in articleList"
            v-bind:key="key"
            @click="addEventListenerNavClick(item,key)">{{item.titleList}}</li>
      </ol>
    </div>
    <div class="article-size article-right-width">
      <div>
        <div class="questions">{{dataQuestion}}</div>
        <div class="answer">{{dataAnswer}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'IndexArticleList',
  props: {
    articleListData: Array
  },
  data () {
    return {
      hotArticle: '文章列表',
      articleLists: true,
      godColor: 0,
      dataQuestion: '弹性布局的设计实现与应用场景',
      dataAnswer: 'yessss',
      articleList: [{
        'id': 1,
        'titleList': '弹性布局',
        'questions': {
          'question': '弹性布局的设计实现与应用场景',
          'answer': 'yessss'
        }
      }, {
        'id': 2,
        'titleList': '栅格布局',
        'questions': {
          'question': '栅格布局的设计实现与应用场景',
          'answer': 'yessss'
        }
      }, {
        'id': 3,
        'titleList': '流式布局',
        'questions': {
          'question': '流式布局的设计实现与应用场景',
          'answer': 'yessss'
        }
      }, {
        'id': 4,
        'titleList': '自适应布局',
        'questions': {
          'question': '自适应布局的设计实现与应用场景',
          'answer': 'yessss'
        }
      }, {
        'id': 5,
        'titleList': '圣杯布局',
        'questions': {
          'question': '圣杯布局的设计实现与应用场景',
          'answer': 'yessss'
        }
      }, {
        'id': 6,
        'titleList': '双飞翼布局',
        'questions': {
          'question': '双飞翼布局的设计实现与应用场景',
          'answer': 'yessss'
        }
      }, {
        'id': 7,
        'titleList': '清除浮动的方式',
        'questions': {
          'question': '清除浮动的方式几种方式',
          'answer': 'yessss'
        }
      }, {
        'id': 8,
        'titleList': '元素居中的方式',
        'questions': {
          'question': '元素居中的几种方式',
          'answer': 'yessss'
        }
      }, {
        'id': 9,
        'titleList': '@media媒体查询',
        'questions': {
          'question': '使用media媒体查询做移动端或pc端自适应',
          'answer': 'yessss'
        }
      }, {
        'id': 10,
        'titleList': '部分手机样式模型参数',
        'questions': {
          'question': '部分手机样式模型参数',
          'answer': 'yessss'
        }
      }]
    }
  },
  methods: {
    addEventListenerNavClick (item, key) {
      // console.log(item.id) // id索引
      for (let i = 0; i < this.articleList.length; i++) {
        if (this.articleList[i].id === item.id) {
          this.dataQuestion = this.articleList[i].questions.question
          this.dataAnswer = this.articleList[i].questions.answer
        }
      }
      this.godColor = key
      // console.log(this.$refs.article[item.id - 1]) // 可以获取对应id下的dom结构
      // console.log(item.titleList)
      // console.log(this.articleList)
    }
  },
  mounted () {

  },
  watch: {
    articleListData () {
      console.log(this.articleListData)
      // console.log(this.articleListData) // 父组件传来的articleList列表
      this.articleList = this.articleListData
      // this.dataQuestion = this.articleListData.questions.question
      // this.dataAnswer = this.articleListData.questions.answer
    }
  }
}
</script>

<style lang='stylus'  scoped>
.article-wrap
  display flex
  width 90%
  margin 0 auto
.article-left-width
  width 18%
  margin-right 5px
  border 1px solid #f3f3f3
  border-radius 12px
.hot-article
  display block
  height 40px
  line-height 40px
  background #f3f3f3
  font-size 20px
  text-align center
  overflow hidden
  color rgb(102, 102, 102)
.article-size
  display inline-block
  min-height 400px
.articleList
  flex-wrap wrap
  min-height 30px
  line-height 30px
  margin-left 40px
  font-style italic
  text-decoration underline
  color rgb(67, 67, 255)
  cursor pointer
.godColor
  color rgb(255, 196, 35)
.article-right-width
  display flex
  width 81%
  padding 0 2% 2% 2%
  border-radius 12px
  flex-direction column
  flex-wrap wrap
.questions
  min-height 50px
  line-height 50px
  background #222222
  padding 0 15px
  border-radius 5px
  color #f2f2f2
.answer
  min-height 40px
  line-height 40px
  margin 0 5px 5px 5px
  padding 15px
  border-radius 5px
  background #f2f2f2
  color #222
</style>



